@mixin about__contact {
  padding: $lem * 2;

  li {
    display: flex;
    align-items: center;

    .glyph {
      width: $lem;
    }

    .long-dash {
      @include long-dash;
      margin: 0 $lem;
    }

    .link {
      @include link(green-on-blue, underline);
    }
  }

  @include about__contact--mobile;
}
