@mixin slider__slide {
  @include transition(transform);

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;

  figure {
    @include figure;

    + figure {
      padding-left: $lem;
    }
  }
}
